.stars {
  display: flex;

  &__item {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url("../images/star.svg");
    background-position: center;
    background-repeat: no-repeat;
    
  }

  &--1 > :nth-child(-n+1),
  &--2 > :nth-child(-n+2),
  &--3 > :nth-child(-n+3),
  &--4 > :nth-child(-n+4),
  &--5 > :nth-child(-n+5) {
    background-image: url("../images/star-active.svg");
  }
}
